/* Base layout */
.page-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #000000;
}

.home {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.content-wrapper {
    width: 100%;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding: 0 20px;
}

/* Section styling */
.section {
    width: 100%;
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

/* Ensure all section content is centered */
.section > div {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Special section styling */
.header-section {
    width: 100%;
    max-width: none;
    margin: 0 0 30px 0;
}

.footer-section {
    margin-top: 40px;
    width: 100%;
    display: flex;
    justify-content: center;
}

/* Responsive styling */
@media screen and (max-width: 840px) {
    .content-wrapper {
        width: 95%;
    }
}

@media screen and (max-width: 480px) {
    .section {
        margin: 15px 0;
    }
    
    .header-section {
        margin-bottom: 20px;
    }
}
  