html, body {
  height: 100%;
  width: 100%;
}

.page-container {
  width: 100%;
  min-height: 100vh;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(100px);
  height: auto;  
  position: relative;
  left: -100px;
}

.home {
  width: calc(100% - 100px);
  display: flex;
  justify-content: center;
  padding: 4rem;
  margin-left: 100px;
  height: auto; 
}

.content-wrapper {
  width: 100%;
  max-width: 800px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  left:150px;
  height: auto;
  top: -125px;
}

.about-me-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 40px 0;
  position: relative;
  left:-250px;
}

.about-me-content {
  width: 100%;
  max-width: 800px;
  display: flex;
  align-items: flex-start;
  gap: 40px;
  padding: 0 20px;
}

/* Green circle */
.profile-circle {
  width: 500px;
  height: 500px;
  background-color: #589E00;
  border-radius: 100%;
  flex-shrink: 0;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5);
  border: 3px solid #466D00;
  position: relative;
  left: -150px;
  top: 00px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Profile image */
.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid black;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.7);
}

/* Profile placeholder text */
.profile-placeholder {
  color: white;
  text-align: center;
  font-family: 'Press Start 2P', monospace;
  font-size: 14px;
  padding: 20px;
  line-height: 1.8;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

/* Text content */
.about-text {
  flex-grow: 1;
  position: relative;
  top: 25px;
  left: -100px;
}

.about-heading {
  color: #589E00;
  font-family: var(--text-heading-1-font-family);
  font-size: var(--text-heading-1-font-size);
  font-style: var(--text-heading-1-font-style);
  font-weight: var(--text-heading-1-font-weight);
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: var(--text-heading-1-letter-spacing);
  line-height: var(--text-heading-1-line-height);
  text-align: left;
}

.about-paragraph {
  color: white;
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  line-height: var(--text-body-text-line-height);
  text-align: left;
  position: relative;
  width: 600px;
}

.about-paragraph p {
  margin-bottom: 15px;
}

/* Responsive design */
@media screen and (max-width: 768px) {
  .about-me-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  
  .about-heading {
    text-align: center;
    font-size: 20px;
  }
  
  .about-paragraph {
    text-align: center;
    font-size: 10px;
  }
  
  .profile-circle {
    width: 150px;
    height: 150px;
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 480px) {
  .profile-circle {
    width: 120px;
    height: 120px;
  }
  
  .about-heading {
    font-size: 18px;
  }
}

